@use 'variables' as v; //para poder usar las variables, importarlas
html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after{
    box-sizing: inherit;
}
body{
 font-family: v.$fuentePrincipal;
 font-size: 1.6rem;

 &.fijar-body{
     overflow: hidden;  //no dart scroll al mostrar la imagen grande
 }

 &.body-scroll{
     padding-top: 8.6rem; //evitar el salto que se da cuando se incluye la barra
 }

}
p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a{
    text-decoration: none;
}
h1, h2, h3{
    margin: 0 0 5rem 0;  
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}
img{
    max-width: 95%;
    width: 100%;
    height: auto;
    display: block;
}
ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
